<template>
  <div class="Book">
    <div class="banner" v-if="HotelInfo.imageurls">
      <div class="swiper-container" id="banner">
        <div class="swiper-wrapper">
          <div class="swiper-slide" v-for="(item,index) in HotelInfo.imageurls" :key="item.id">
            <img :src="item.path" alt="">
          </div>
        </div>
        <div class="swiper-pagination"></div>
      </div>
      <div class="text" v-if="HotelInfo.noticeEnable==0">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-gonggao"></use>
        </svg>
        <marquee scrollamount="4" direction="left">{{HotelInfo.bulletin}}</marquee>
      </div>
    </div>
    <div class="weui-cells information">
      <a class="weui-cell weui-cell_access" :href="'tel:'+HotelInfo.phone">
        <div class="weui-cell__hd">
          <svg class="icon" aria-hidden="true">
            <use xlink:href="#icon-dianhuahaoma"></use>
          </svg>
        </div>
        <div class="weui-cell__bd">
          <p>{{HotelInfo.phone}}</p>
        </div>
        <div class="weui-cell__ft"></div>
      </a>
      <a class="weui-cell weui-cell_access" href="javascript:;" @click="mapfn">
        <div class="weui-cell__hd">
          <svg class="icon" aria-hidden="true">
            <use xlink:href="#icon-weizhi"></use>
          </svg>
        </div>
        <div class="weui-cell__bd">
          <p>{{HotelInfo.address}}</p>
        </div>
        <div class="weui-cell__ft"></div>
      </a>
      <a class="weui-cell weui-cell_access details" href="javascript:;" @click="dialog1=!dialog1">
        <div class="weui-cell__hd">
          <svg class="icon" aria-hidden="true" v-for="item in HotelInfo.installations" :key="item.id">
            <use :xlink:href="'#'+item.iconUrl"></use>
          </svg>
        </div>
        <div class="weui-cell__bd">
        </div>
        <div class="weui-cell__ft">/</div>
      </a>
    </div>
    <div class="weui-flex timebox">
      <div class="weui-flex__item">
        <div class="placeholder" @click="dialog3=true">
          <span class="time">{{day1}}</span>
          <span>{{Today}}</span>
          <span class="fr"></span>
        </div>
      </div>
      <div class="weui-flex__item" @click="dialog3=true">
        <div class="placeholder">
          <span class="time">{{day2}}</span>
          <span>{{Tomorrow}}</span>
          <span class="fr"></span>
        </div>
      </div>
    </div>
    <div class="swiper-container activeRegion" v-if="activitylist">
      <div class="swiper-wrapper">
        <div class="swiper-slide" v-for="item in activitylist" :key="item.id">
          <router-link :to="'/book/activitydetails/'+item.activityType+ '/'+ item.id" tag="div" class="weui-cell ">
            <div class="weui-cell__hd ">
              <img :src="item.path " />
            </div>
            <div class="weui-cell__bd ">
              <div class="title " v-if="item.activityType==1 ">
                <span class="special "></span>
              </div>
              <div class="title " v-if="item.activityType==2 ">
                <span class="Hour "></span>
              </div>
              <div class="title " v-if="item.activityType==3 ">
                <span class="Seckill "></span>
              </div>
              <div class="title " v-if="item.activityType==4 ">
                <span class="group "></span>
              </div>
              <div class="newPrice clearfix " v-if="(item.activityType==1)||(item.activityType==3) ">
                <span>
                  <i></i>{{item.activityPrice |money}}</span> 
              </div>

              <div class="newPrice clearfix " v-if="item.activityType==2 ">
                {{item.checkInTime |toHourMinute}}/
                <span>
                  <i></i>{{item.activityPrice |money}}</span> 
              </div>
              <div class="newPrice clearfix " v-if="item.activityType==4 ">
                <span>
                  <i></i>{{item.activityPrice |money}}</span>  /{{item.minPurchaseNumber}}
              </div>
              <div class="tiem newtime " ref="newtime ">:00000000</div>
            </div>
          </router-link>
        </div>
      </div>
    </div>
    <div class="weui-cells room-list ">
      <div class="weui-cell " v-for="(item,index) in Categorydata" :key="index " @click="opendetails(item.id) ">
        <div class="weui-cell__hd ">
          <img :src="item.images[0]" />
        </div>
        <div class="weui-cell__bd ">
          <div class="title ">{{item.name}}
            <a class="weui-btn_mini weui-btn weui-btn_plain-primary " v-if="item.remnantRoomEnable==0 ">{{item.spare}}</a>
          </div>
          <div class="details ">
            <div class="fl ">
              <p class="equipment ">
                <template v-if="item.breakfastEnable==0 ">{{item.breakfastQuantity}}</template>
                <template v-if="item.breakfastEnable==1 "></template>
                {{options[item.Bed]}} {{item.acreage}}m
                <sup>2</sup>
              </p>
              <p class="gray "></p>
              <p class="Price ">
                <span class="newPrice ">
                  <i></i>{{item.displayPrice |money}}
                </span>
              </p>
            </div>
            <router-link to="/bookImmediately " tag="a" class="weui-btn_mini weui-btn weui-btn_plain-primary fr "></router-link>
          </div>
        </div>
      </div>
    </div>
    <!--  -->
    <!-- / -->
    <div class="downDialog ">
      <div class="mask " v-if="dialog1 "></div>
      <transition name="dynamic ">
        <div class=" Hoteldetails Dialogbody " v-if="dialog1 ">
          <div class="downDialog-top ">
            <i class="weui-icon-cancel " @click="dialog1=false "></i>
            /
          </div>
          <div class="downDialog_content ">
            <div class="region ">
              <h4 class="title mt15 "></h4>
              <div class="weui-flex ">
                <div class="weui-flex__item " v-for="item in HotelInfo.installations " :key="item.id ">
                  <div class="placeholder ">
                    <svg class="icon " aria-hidden="true ">
                      <use :xlink:href=" '#'+item.iconUrl "></use>
                    </svg>{{item.name}}</div>
                </div>
              </div>
            </div>
            <div class="region ">
              <h4 class="title "></h4>
              <p class="explain ">{{HotelInfo.desc}}</p>
            </div>
          </div>
        </div>
      </transition>
    </div>
    <!--  -->
    <div class="downDialog ">
      <div class="mask " v-if="dialog2 "></div>
      <transition name="dynamic ">
        <div class=" Roomsdetails Dialogbody " v-if="dialog2 ">
          <div class="downDialog-top ">
            <i class="weui-icon-cancel " @click="dialog2=false "></i>
            
          </div>
          <div class="downDialog_content ">
            <div class="weui-dialog__bd ">
              <div class="swiper-container " id="Roomsswiper ">
                <div class="swiper-wrapper ">
                  <div class="swiper-slide " v-for="(item,index) in Categorydetails.images " :key="index ">
                    <img :src="item.path " alt=" ">
                  </div>
                </div>
                <!-- Add Pagination -->
                <div class="swiper-pagination " id="Roomspagination "></div>
              </div>
              <div class="column ">
                <div class="weui-flex ">
                  <div class="weui-flex__item ">
                    <div class="placeholder ">
                      <template v-if="Categorydetails.breakfastEnable==0 ">{{Categorydetails.breakfastQuantity}}</template>
                      <template v-if="Categorydetails.breakfastEnable==1 "></template>
                    </div>
                  </div>
                  <div class="weui-flex__item ">
                    <div class="placeholder ">{{Internet[Categorydetails.Internet]}}</div>
                  </div>
                </div>
                <div class="weui-flex ">
                  <div class="weui-flex__item ">
                    <div class="placeholder ">{{options[Categorydetails.Bed]}}
                      <template v-if="Categorydetails.text2&&Categorydetails.text3 ">{{Categorydetails.text2}}x{{Categorydetails.text3}}m </template>2</div>
                  </div>
                  <div class="weui-flex__item ">
                    <div class="placeholder ">{{bathroom[Categorydetails.bathroom]}}</div>
                  </div>
                </div>
                <div class="weui-flex ">
                  <div class="weui-flex__item ">
                    <div class="placeholder ">
                      <template v-if="Categorydetails.people ">{{Categorydetails.people}}</template>
                    </div>
                  </div>
                  <div class="weui-flex__item ">
                    <div class="placeholder ">{{Windows[Categorydetails.Window]}}</div>
                  </div>
                </div>
                <div class="weui-flex ">
                  <div class="weui-flex__item ">
                    <div class="placeholder ">
                      <template v-if="Categorydetails.acreage ">{{Categorydetails.acreage}}m
                        <sup>2</sup>
                      </template>

                    </div>
                  </div>
                  <div class="weui-flex__item ">
                    <div class="placeholder ">{{smoke[Categorydetails.smoke]}}</div>
                  </div>
                </div>
              </div>
              <div class="text ">
                <h4 class="title mt10 "></h4>
                <p> / </p>
                <h4 class="title mt10 "></h4>
                <p>{{Categorydetails.deposit|money}} </p>
                <h4 class="title mt10 "></h4>
                <p>{{Categorydetails.desc}}</p>
              </div>
            </div>
            <div class="weui-dialog__ft ">
              <div class="newPrice fl ">
                <i></i>{{item.displayPrice |money}}
              </div>
              <button class="weui-btn weui-btn_mini fr "></button>
            </div>
          </div>
        </div>
      </transition>
    </div>
    <!--  -->
    <div class="downDialog ">
      <div class="mask " v-if="dialog3 "></div>
      <transition name="dynamic ">
        <div class=" calendarDialog Dialogbody " v-if="dialog3 ">
          <div class="downDialog-top ">
            <i class="weui-icon-cancel " @click="dialog3=false "></i>
            
          </div>
          <div class="downDialog_content ">
            <Calendar :Calendardata.sync='Calendardata'></Calendar>
          </div>
        </div>
      </transition>
    </div>
  </div>
</template>

<script>
import Calendar from "@/components/Calendar";
import { roomBooking } from "@/assets/js/request.js";
import { DateFormat } from "@/assets/js/filters.js";
export default {
  data() {
    return {
      HotelInfo: "",
      hotelId: this.$store.state.hotelId,
      activitylist: [],
      dialog1: false,
      dialog2: false,
      dialog3: false,
      abjda: "",
      timer: null,
      Categorydata: null,
      day1: "",
      day2: "",
      roomInTime: null,
      roomOutTime: null,
      Today: "",
      Tomorrow: "",
      //
      options: ["", "", ""],
      Internet: ["WIFI", "", "WIFI/"],
      bathroom: ["", ""],
      Windows: ["", ""],
      smoke: ["", ""],
      Categorydetails: "",
      Calendardata: ""
    };
  },
  components: {
    Calendar
  },
  mounted() {
    this.$store.dispatch("getHotelInfo").then(() => {
      this.HotelInfo = this.$store.state.HotelInfo;
      new Swiper("#banner", {
        loop: true,
        // 
        // autoplay: 5000,
        pagination: "#banner .swiper-pagination"
      });
    });
    this.day1 = this.GetDateStr(0, true);
    this.day2 = this.GetDateStr(1, true);
    this.roomInTime =
      this.GetDateStr(0)
        .replace("", "-")
        .replace("", "-")
        .replace("", "") + " 00:00:00";
    this.roomOutTime =
      this.GetDateStr(1)
        .replace("", "-")
        .replace("", "-")
        .replace("", "") + " 00:00:00";

    //
    roomBooking.activitylist({
      Suffix: this.hotelId + "/activity",
      fn: data => {
        var _this = this;
        this.activitylist = data.records;
        setTimeout(function() {
          new Swiper(".activeRegion", {
            slidesPerView: "auto",
            centeredSlides: true,
            paginationClickable: true,
            spaceBetween: 10,
            loop: true,
            autoplay: 5000,
            autoplayDisableOnInteraction: false,
            onSlideChangeStart: function(swiper) {
              clearInterval(_this.timer);
              _this.countDown(
                _this.activitylist[
                  swiper.activeIndex % _this.activitylist.length
                ].endTime
              );
            }
          });
        }, 100);
      }
    });
    //
    this.getRoomCategory();
  },
  watch: {
    Calendardata: function(val, oldVal) {
      if (val) {
        this.roomInTime =
          val[0]
            .replace("", "-")
            .replace("", "-")
            .replace("", "") + " 00:00:00";
        this.roomOutTime =
          val[1]
            .replace("", "-")
            .replace("", "-")
            .replace("", "") + " 00:00:00";
        this.getRoomCategory();
        if (this.GetDateStr(0) == val[0]) {
          this.Today = "";
        } else if (this.GetDateStr(1) == val[0]) {
          this.Today = "";
        } else {
          switch (new Date(this.roomInTime).getDay()) {
            case 0:
              this.Today = "";
              break;
            case 1:
              this.Today = "";
              break;
            case 2:
              this.Today = "";
              break;
            case 3:
              this.Today = "";
              break;
            case 4:
              this.Today = "";
              break;
            case 5:
              this.Today = "";
              break;
            case 6:
              this.Today = "";
              break;
          }
        }
        if (this.GetDateStr(1) == val[1]) {
          this.ToTomorrowday = "";
        } else if (this.GetDateStr(1) == val[1]) {
          this.Tomorrow = "";
        } else {
          console.log(new Date(this.roomOutTime).getDay());
          switch (new Date(this.roomOutTime).getDay()) {
            case 0:
              this.Tomorrow = "";
              break;
            case 1:
              this.Tomorrow = "";
              break;
            case 2:
              this.Tomorrow = "";
              break;
            case 3:
              this.Tomorrow = "";
              break;
            case 4:
              this.Tomorrow = "";
              break;
            case 5:
              this.Tomorrow = "";
              break;
            case 6:
              this.Tomorrow = "";
              break;
          }
        }
      }
      this.dialog3 = false;
    }
  },
  methods: {
    countDown(val) {
      var _this = this;
      var abc = function(params) {
        var utc = (val - new Date().getTime()) / 1000;
        if (utc <= 0) {
          var doms = document.getElementsByClassName("newtime");
          for (var i = 0; i < doms.length; i++) {
            doms[i].innerHTML =
              ": " + "00" + "" + "00" + "" + "00" + "" + "00" + "";
          }
        } else {
          var tian = Math.floor(utc / (24 * 60 * 60));
          var shi = Math.floor((utc / 60 / 60) % 24);
          var fen = Math.floor((utc / 60) % 60);
          var miao = Math.floor(utc % 60);
          if (tian < 10) {
            tian = "0" + tian;
          }
          if (shi < 10) {
            shi = "0" + shi;
          }
          if (fen < 10) {
            fen = "0" + fen;
          }
          if (miao < 10) {
            miao = "0" + miao;
          }
          var doms = document.getElementsByClassName("newtime");
          for (var i = 0; i < doms.length; i++) {
            doms[i].innerHTML =
              ": " + tian + "" + shi + "" + fen + "" + miao + "";
          }
        }
      };
      abc();
      _this.timer = setInterval(function() {
        abc();
      }, 1000);
    },
    //
    opendetails(id) {
      this.Categorydetails = "";
      this.Categorydata.forEach(function(item) {
        if (item.id == id) {
          this.Categorydetails = item;
        }
      }, this);
      this.dialog2 = true;

      this.Categorydetails.bathroom = JSON.parse(
        this.Categorydetails.infrastructureRelations[4].displayValue
      ).bathroom;
      this.Categorydetails.Bed = JSON.parse(
        this.Categorydetails.infrastructureRelations[0].displayValue
      ).text1;
      this.Categorydetails.text2 = JSON.parse(
        this.Categorydetails.infrastructureRelations[0].displayValue
      ).text2;
      this.Categorydetails.text3 = JSON.parse(
        this.Categorydetails.infrastructureRelations[0].displayValue
      ).text3;
      this.Categorydetails.text4 = JSON.parse(
        this.Categorydetails.infrastructureRelations[0].displayValue
      ).text4;
      this.Categorydetails.Internet = JSON.parse(
        this.Categorydetails.infrastructureRelations[3].displayValue
      ).Internet;
      this.Categorydetails.people = JSON.parse(
        this.Categorydetails.infrastructureRelations[2].displayValue
      ).people;
      this.Categorydetails.Window = JSON.parse(
        this.Categorydetails.infrastructureRelations[5].displayValue
      ).Window;
      this.Categorydetails.acreage = JSON.parse(
        this.Categorydetails.infrastructureRelations[1].displayValue
      ).acreage;
      this.Categorydetails.smoke = JSON.parse(
        this.Categorydetails.infrastructureRelations[6].displayValue
      ).smoke;
      setTimeout(function() {
        new Swiper("#Roomsswiper", {
          pagination: "#Roomspagination",
          paginationType: "fraction",
          loop: true
        });
      }, 50);
    },
    //
    getRoomCategory() {
      roomBooking.roomCategory({
        Suffix: this.hotelId + "/roomCategory",
        params: {
          hotelId: this.hotelId,
          roomInTime: this.roomInTime,
          roomOutTime: this.roomOutTime
        },
        fn: data => {
          this.Categorydata = data.records;
          this.Categorydata.forEach(function(item) {
            item.Bed = JSON.parse(
              item.infrastructureRelations[0].displayValue
            ).text1;
            item.acreage = JSON.parse(
              item.infrastructureRelations[1].displayValue
            ).acreage;
            item.spare = item.roomCount - item.orderCount;
          }, this);
        }
      });
    },
    GetDateStr(AddDayCount, fayg) {
      var dd = new Date();
      dd.setDate(dd.getDate() + AddDayCount); //AddDayCount
      var y = dd.getFullYear();
      var m = dd.getMonth() + 1; //
      var d = dd.getDate();
      if (m < 10) {
        m = "0" + m;
      }
      if (d < 10) {
        d = "0" + d;
      }
      if (fayg) {
        return m + "" + d + "";
      } else {
        return y + "" + m + "" + d + "";
      }
    },
    mapfn() {
      location.href =
        "http://apis.map.qq.com/tools/routeplan/eword=" +
        this.HotelInfo.address +
        "&epointx=" +
        this.HotelInfo.longitude +
        "&epointy=" +
        this.HotelInfo.latitude +
        "?referer=mapqq&key=GQKBZ-BKZW6-ZCVSQ-MD5GX-MYNBQ-LSBPK";
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style   lang='less' rel='stylesheet/less'>
/*  */

/*  */

.dynamic-enter-active {
  transition: all 0.5s ease;
}

.dynamic-leave-active {
  transition: all 0.5s ease;
}

.dynamic-enter,
.dynamic-leave-to {
  opacity: 0;
  transform: translateY(100%);
}

.mask {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 3;
}

.Calendar-mask {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 4;
}
</style>



